<template>
  <el-container style="height: 100vh; margin: 0; padding: 0;">
    <!-- 左侧菜单 -->
    <el-aside width="220px" style="background-color: #2D3A4B; padding: 0;">
      <el-menu default-active="1" class="el-menu-vertical-demo" background-color="#2D3A4B" text-color="#fff" active-text-color="#ffd04b">
        <el-menu-item index="1">Dashboard</el-menu-item>
        <el-menu-item index="2">Recipes</el-menu-item>
        <el-menu-item index="3">Ingredients</el-menu-item>
        <el-menu-item index="4">Settings</el-menu-item>
      </el-menu>
    </el-aside>

    <!-- 右侧内容 -->
    <el-container style="padding: 0; margin: 0;">
      <el-header style="background-color: #f5f7fa; text-align: center; font-size: 20px; padding: 15px 0;">
        Data Statistics
      </el-header>
      <el-main style="padding: 20px;">
        <el-row :gutter="20">
          <el-col :span="8">
            <el-card>
              <template v-slot:header>
                Total Recipes
              </template>
              <p>{{ recipesCount }}</p>
            </el-card>
          </el-col>
          <el-col :span="8">
            <el-card>
              <template v-slot:header>
                Total Ingredients
              </template>
              <p>{{ ingredientsCount }}</p>
            </el-card>
          </el-col>
        </el-row>
      </el-main>
      <el-footer style="text-align: center; background-color: #f5f7fa;">
        © 2024 Your Company. All rights reserved.
      </el-footer>
    </el-container>
  </el-container>
</template>

<script>
import axios from "axios";

export default {
  name: "AppDashboard",
  data() {
    return {
      recipesCount: 0, // 初始为0
      ingredientsCount: 200, // 假设这里的数据来源固定
    };
  },
  mounted() {
    // 页面加载时获取数据
    this.getRecipesCount();
  },
  methods: {
    async getRecipesCount() {
      try {
        const response = await axios.get("/api/recipes/count");
        this.recipesCount = response.data;
      } catch (error) {
        console.error("Failed to fetch recipes count:", error);
      }
    },
  },
};
</script>

<style scoped>
.el-menu-vertical-demo {
  width: 100%;
  min-height: 100%;
  padding-top: 20px;
}
.el-menu-item {
  padding-left: 24px !important;
}
</style>
